* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #232323;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  min-height: 1100px;
  position: relative;
}

img {
  width: 70px;
  height: 70px;
  border-radius: 5px;
}

.left {
  position: absolute;
  width: 228px;
  height: 275px;
  left: 41px;
  top: 43px;
  background: #131313;
  border-radius: 15px;
}

.left p {
  text-transform: capitalize;
  color: #a5a5a5;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  margin: 1rem 0 0.7rem 1.5rem;
}

.line {
  height: 1px;
  width: 80%;
  margin: 0 auto 0.5rem;
  background-color: #a5a5a5;
}

.drop {
  width: 80%;
  height: 70%;
  margin: 0 auto;
  padding: 0.25rem 0.15rem;
}

.dropbtn {
  background: #232323;
  border-radius: 5px;
  padding: 0.35rem;
  font-size: 17px;
  cursor: pointer;
  width: 168px;
  height: 38px;
  position: relative;
  text-align: start;
  outline: 0;
  border: none;
  color: #fff;
}

.sort {
  position: absolute;
  top: 5px;
  right: 10px;
  color: #a5a5a5;
}

.dropdown {
  position: relative;
  display: inline-block;
  margin: 0.75rem auto;
}

.dropMenu {
  position: absolute;
  background: #232323;
  color: #fff;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropItem {
    display: block;
    color: #fff;
    text-decoration: none;
    padding: 12px 16px;
}

.dropItem:hover {
    color: #fff;
}

/* Aside */
.aside {
    position: absolute;
    left: 303px;
    width: 75vw;
    top: 43px;
}

.header h1 {
    text-transform: capitalize;
    margin-bottom: 1rem;
    color: rgba(255, 255, 255, 0.87);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.header p {
    text-transform: capitalize;
    color: #a5a5a5;
    margin-bottom: 0.75rem;
    font-size: 25px;
    font-weight: 500;
}

.name {
    text-transform: capitalize;
    color: #fff;
    font-size: 20px;;
}

.line2 {
    width: 984px;
    height: 1px;
    background-color: #a5a5a5;
    margin: 0.85rem 0;
}

/* ProductBox */

.productbox {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    width: 984px;
    height: 230px;
    background-color: #131313;
    padding: 1rem;
    border-radius: 15px;
    overflow: hidden;
}

/* Products */
.box {
    margin-bottom: 1rem;
    width: 210px;
    height: 200px;
    background-color: #232323;
    border-radius: 7px;
    cursor: pointer;
    transition: all 0.3s linear;
}

.box:hover {
    transform: scale(1.05);
}

.upper {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0.3rem auto 0.1rem;
}

ul {
    padding: 0.3rem;
}

li {
    list-style-type: none;
    text-transform: capitalize;
    line-height: 25px;
}

li:nth-child(2) {
    font-size: 13px;
    color: rgba(255, 255, 255, 0.6);
}

li:nth-child(3) {
    font-size: 13px;
    color: #fff;
}

.lower {
    line-height: 23px;
    padding: 0 0.5rem;
    color: rgba(255, 255, 255, 0.6);
    font-size: 13px;
}

.lower p span {
    margin-left: 1rem;
}

.lower .desc {
    font-size: 11px;
}